@import "helpers/reset";
@import "helpers/mixins";

$black: #222831;
$white: #ffffff;
$lightGrey: #eeeeee;
$badBlue: #4f8a8b;
$yellow: #fbd46d;
$orange: #e65c00;
$red: #FF0043;
$transition: all 0.3s ease;

@keyframes fade {
  from {
    width: 0; }
  to {
    width: 100%; } }

@keyframes fadeHide {
  from {
    width: 100%; }
  to {
    width: 0; } }

@keyframes show {
  from {
    transform: translateY(-100%);
    opacity: 0; }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes hide {
  from {
    transform: translateY(0);
    opacity: 1; }
  to {
    opacity: 0;
    transform: translateY(100%); } }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: none; }

body {
  font-family: Raleway;
  font-weight: 500;
  letter-spacing: 1.15px;
  color: black; }

svg {
  width: 100%;
  height: 100%; }

a {
  text-decoration: none;
  color: white;
  transition: $transition;
  &:hover,
  &:focus {
    color: $orange;
    cursor: none; } }

picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

p {
  line-height: 2rem; }

.link {
  color: white;
  width: 60%;
  text-align: center;
  transition: $transition;
  font-size: 1.35vw;
  position: relative;
  display: block;
  border-bottom: 2px solid $orange;

  &:before {
    content: '';
    display: block;
    position: absolute;
    height: 5%;
    width: 0;
    bottom: 0;
    left: 0;
    background-color: $orange;
    z-index: 2;
    animation: fadeHide 0.5s ease-in-out forwards; }

  &:hover,
  &:focus {
    color: white;
    span {
      color: white;
      position: relative;
      z-index: 3; }
    &:before {
      animation: fade 1s ease-in-out forwards; } } }

.header {
  height: 120vh;
  padding: 7.5% 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background-color: black;
  color: white;
  position: relative; }

.navigation-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  background-color: black;
  padding: 0 12.5%;
  display: flex;
  width: 100%;
  margin: 0 auto;
