  &__link {
    position: relative;
    width: 100%;
    padding: 1rem;
    color: white;
    text-align: center;
    font-size: 1.15vw;
    font-weight: 600;

    &:before {
      content: '';
      display: block;
      width: 0;
      height: 100%;
      top: 0;
      left: 0;
      background-color: $orange;
      position: absolute;
      z-index: 2;
      animation: fadeHide 0.5s ease-in-out forwards; }

    &:hover,
    &:focus {
      color: white;
      span {
