  width: 50%;
  height: inherit;

  &-background {
    position: absolute;
    right: 10%;
    top: 0;
    width: 100%;
    transform: scale(1.05);
    height: auto;
    transition: $transition;
    img {
      width: 100%;
      height: 100%; } } }

.main-content {
  width: 100%; }

.container {
  width: inherit;
  height: inherit;
  padding: 0 15%; }

.section {
