    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      object-fit: contain;
      transition: $transition;
      transform: translateY(-100%);
      &:first-child {
        transform: translate(0%, 0%);
