      transform: scale(1.2); } } }

.show {
  opacity: 1; }

.transform {
  transform: translateX(0); }

@keyframes position {
  from {
    opacity: 0;
    transform: translateY(-100%); }

  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes blured {
