    transform: translate(-50%, 0); } }

.stages_active {
  visibility: visible;
  display: flex;
  animation: move 2.5s ease forwards;
  pointer-events: all;
  transition: $transition; }

.customer-logos {
  width: 100%;
  height: 35vh;
  display: flex;
  align-items: center; }

.slick-track {
  height: 35vh; }

.slick-slide {
  margin: 0px 20px;
  display: none;
  float: left;
  height: 90%;
  min-height: 1px;
  border-radius: 50%;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block; }
  .slick-loading {
    img {
      display: none; } }
  .dragging {
