  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
  &:focus {
    outline: none; }
  .dragging {
    cursor: no-drop; } }

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  &:before,
  &:after {
    display: flex;
    content: ''; }
  &:after {
    clear: both; } }

.slick-loading {
  .slick-track {
    visibility: hidden; }
  .slick-slide {
    visibility: hidden; } }

[dir='rtl'] .slick-slide {
  float: right; }

.slick-initialized .slick-slide {
  display: block; }

.slick-vertical .slick-slide {
  display: block;
  height: auto; }

.slick-arrow.slick-hidden {
  display: none; }

.cursor {
  pointer-events: none;

  &__ball {
    position: absolute;
    top: 0;
    left: 0;
    mix-blend-mode: difference;
    z-index: 9999; }

  &__ball {
    circle {
      fill: $orange;
      color: white; } } }

.more {
  width: 100%;
  background-color: black;
  color: white;
  padding: 5% 0;
  overflow: hidden;

  &-wrapper {
    width: inherit;
    height: 100%;
    display: flex;
    flex-direction: column; }

  &__title {
    font-weight: 600;
    font-size: 3.5vw;
    &:first-letter {
      color: $orange; } }

  &-navigation {
    padding: 2rem;
    width: auto;
    justify-content: flex-start;
    align-items: center;
    display: flex;

    &__item {
      list-style: none;
      width: auto;
      height: 100%;
      color: white;
      text-align: center;
      font-size: 1.35vw;
      padding: 1rem 2.5rem;
      position: relative;
      font-weight: bold;

      &:before {
        content: '';
        display: block;
        width: 0;
        height: 100%;
        top: 0;
        left: 0;
        background-color: $orange;
        position: absolute;
        z-index: 2;
        animation: fadeHide 0.5s ease-in-out forwards; }

      &:hover,
      &:focus {
        color: white;
        span {
          color: white;
          position: relative;
          z-index: 3; }
        &:before {
          animation: fade 1s ease-in-out forwards; } } } }

  &-list {
    width: inherit;
    height: 75vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;

    &__item {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      visibility: hidden;
      opacity: 0;
      transform-origin: bottom left;
      animation: dropped 2s ease forwards;

      a {
        width: inherit;
        height: inherit;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          width: 95%;
          height: 100%; } } } } }

.active {
  visibility: visible;
  transform-origin: top right;
  animation: blured 2.5s ease forwards;
  transition: all 0.5s ease;
  pointer-events: all; }

.hero-stages__video-item {
  position: relative;
  top: 0;
  left: 0;
  transform: translateX(-50%);
  justify-content: space-between;
  align-items: flex-start;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  display: none; }

.video-item--active {
  visibility: visible;
  opacity: 1;
  animation: position 1s ease forwards;
  transition: all 0.5s ease;
  pointer-events: all;
  left: 0;
  top: 0; }

.modal {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgba($black, 0.9);
  z-index: 9998;
  transform: translateY(-100%);
  opacity: 0;
  visibility: hidden;
  transition: $transition;

  &__form {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3.5rem 2rem;
    width: 50rem;
    border-radius: 15px;
    text-align: center;
    background-color: rgba($black, 0.85);
    box-shadow: 0 0 30px 1px rgba($orange, 0.4); }

  &__title {
    padding-bottom: 2rem;
    color: white;
    font-size: 3.25rem;
    width: 80%; }

  &__input {
    padding: 1.5rem 2rem;
    margin: 1rem 0;
    font-size: 1.35vw;
    border: 1px solid $orange;
    transition: $transition;

    &:hover {
      box-shadow: 0 1px 15px 0 $orange; } }

  &__btn {
    padding: 1rem 2.5rem;
    background: {
      color: $yellow;
      image: linear-gradient(to right, #ffe259, #ffa751); }
    border-radius: 5px;
    border: none;
    -webkit-backface-visibility: hidden;
    z-index: 1;
    width: 50%;
    text-align: center;
    transition: $transition;
    position: relative;
    color: $black;
    font-size: 2rem;
    margin-top: 2.5rem;
    &:after {
      content: '';
      position: absolute;
      display: block;
      border-radius: 5px;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      box-shadow: 0 5px 35px 1px rgba(255, 180, 0, 0.5);
      background: linear-gradient(to right, #e65c00, #f9d423);
      transition: all 1s ease-out;
      z-index: 2;
      opacity: 0; }

    &:hover, &:focus {
      color: $white; } } }

.btn--close {
  display: flex;
  text-align: center;
  width: 5rem;
  height: 5rem;
  background-color: transparent;
  transition: $transition;
  font-size: 3.5rem;
  border: none;
  justify-content: center;
  align-items: center;
  line-height: 4rem;
  position: absolute;
  top: 5%;
  left: 95%;
  color: white;
  transform: translateX(-95%);
  &:hover {
    background-color: $orange; } }

.modal--active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0); }

.scroll--hidden {
  overflow: hidden; }

.btn--consultation {
  background-color: transparent;
  border: {
    left: none;
    right: none;
    top: none; } }



