html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@keyframes fade {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes fadeHide {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}

@keyframes show {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes hide {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translateY(100%);
  }
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: none;
}

body {
  font-family: Raleway;
  font-weight: 500;
  letter-spacing: 1.15px;
  color: black;
}

svg {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
  color: white;
  transition: all 0.3s ease;
}

a:hover, a:focus {
  color: #e65c00;
  cursor: none;
}

picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  line-height: 2rem;
}

.link {
  color: white;
  width: 60%;
  text-align: center;
  transition: all 0.3s ease;
  font-size: 1.35vw;
  position: relative;
  display: block;
  border-bottom: 2px solid #e65c00;
}

.link:before {
  content: '';
  display: block;
  position: absolute;
  height: 5%;
  width: 0;
  bottom: 0;
  left: 0;
  background-color: #e65c00;
  z-index: 2;
  animation: fadeHide 0.5s ease-in-out forwards;
}

.link:hover, .link:focus {
  color: white;
}

.link:hover span, .link:focus span {
  color: white;
  position: relative;
  z-index: 3;
}

.link:hover:before, .link:focus:before {
  animation: fade 1s ease-in-out forwards;
}

.header {
  height: 120vh;
  padding: 7.5% 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  background-color: black;
  color: white;
  position: relative;
}

.navigation-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  background-color: black;
  padding: 0 12.5%;
  display: flex;
  width: 100%;
  margin: 0 auto;
  height: 7rem;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
}

.logo {
  width: 25%;
  height: 80%;
  padding: 1rem 0;
}

.logo img {
  width: 100%;
  height: 100%;
}

.menu {
  display: none;
}

.navigation {
  display: flex;
  width: 80%;
  margin: 0 auto;
  height: 100%;
  justify-content: space-between;
  transition: all 2.5s ease-in-out;
}

.navigation__item {
  height: inherit;
  width: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navigation__link {
  position: relative;
  width: 100%;
  padding: 1rem;
  color: white;
  text-align: center;
  font-size: 1.15vw;
  font-weight: 600;
}

.navigation__link:before {
  content: '';
  display: block;
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #e65c00;
  position: absolute;
  z-index: 2;
  animation: fadeHide 0.5s ease-in-out forwards;
}

.navigation__link:hover, .navigation__link:focus {
  color: white;
}

.navigation__link:hover span, .navigation__link:focus span {
