  right: 10%;
  top: 0;
  width: 100%;
  transform: scale(1.05);
  height: auto;
  transition: all 0.3s ease;
}

.parallax-background img {
  width: 100%;
  height: 100%;
}

.main-content {
  width: 100%;
}

.container {
  width: inherit;
  height: inherit;
  padding: 0 15%;
}

.section {
