}

.services-group__title:first-letter {
  color: #e65c00;
  font-size: 5.25vw;
}

.services-group-wrapper {
  width: 100%;
  height: auto;
  display: grid;
  grid-gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
}

.services-group__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 3.5rem;
  justify-content: space-between;
  transition: all 0.3s ease;
