}

.services {
  background-color: #eeeeee;
  background-image: linear-gradient(to top, #ece9e6, #ffffff);
  border-radius: 5px;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.services:first-child .services__icon {
  display: none;
}

.services:first-child:hover, .services:first-child:focus {
  overflow: visible;
}

.services:first-child:hover .video-wrapper, .services:first-child:focus .video-wrapper {
  animation: show 1s ease forwards;
}

.services__icon {
  width: 7.5rem;
  height: 7.5rem;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.services__title {
  padding: 2rem 0;
  font-size: 2vw;
  display: flex;
  position: relative;
  font-weight: 600;
}

.services__title:after {
  display: block;
  content: '';
  width: 20%;
  height: 2px;
  left: 0;
  bottom: 15%;
  background-color: #e65c00;
  position: absolute;
}

.services__description {
  font-size: 1.1vw;
  width: 100%;
}

.services__list-menu {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 1.5rem 0;
}

.services__list-menu li {
  list-style: inside;
  padding: 0.75rem 0;
  font-size: 1vw;
}

.services__link {
  background-color: #fbd46d;
  background-image: linear-gradient(to right, #ffe259, #ffa751);
  border-radius: 5px;
  -webkit-backface-visibility: hidden;
  z-index: 1;
  padding: 1.25rem 2.5rem;
  width: 50%;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
  border: none;
}

.services__link span {
  color: black;
  font-size: 1.75rem;
  transition: all 0.3s ease;
}

.services__link:after {
  content: '';
  position: absolute;
  display: block;
  border-radius: 5px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 5px 35px 1px rgba(255, 180, 0, 0.5);
  background: linear-gradient(to right, #e65c00, #f9d423);
  transition: all 1s ease-out;
  z-index: 2;
  opacity: 0;
}

.services__link:hover span, .services__link:focus span {
  position: relative;
  color: white;
  z-index: 3;
}

.services__link:hover:after, .services__link:focus:after {
  opacity: 1;
}

.video_show {
  animation: show 1s ease forwards;
}

.video-wrapper {
  transition: all 1s ease;
  animation: hide 1s ease;
  transform: translateY(-100%);
  position: absolute;
  top: -10%;
  right: 10%;
  width: 25rem;
  height: 110%;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
}

.video__photo-back {
  width: 100%;
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  background-color: black;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
}

.video__photo-back video {
  width: 97.5%;
  height: 90%;
