  z-index: 3;
}

.stages-navigation__link:hover:before, .stages-navigation__link:active:before, .stages-navigation__link:focus:before {
  animation: fade 1s ease-in-out forwards;
}

.hero-stages {
  width: 100%;
  position: relative;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 0;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  display: none;
}

.hero-stages__title {
  font-weight: 600;
  font-size: 2.5vw;
  position: relative;
  padding-bottom: 1.75rem;
}

.hero-stages__title:after {
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
