}

.hero-stages__video picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
  transition: all 0.3s ease;
  transform: translateY(-100%);
}

.hero-stages__video picture:first-child {
  transform: translate(0%, 0%);
  opacity: 1;
  visibility: visible;
}

.hero-stages__video picture:hover, .hero-stages__video picture:focus {
  transform: scale(1);
}

.hero-stages__video picture img {
