}

.show {
  opacity: 1;
}

.transform {
  transform: translateX(0);
}

@keyframes position {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes blured {
  0% {
    opacity: 0;
    transform: rotateX(90deg);
  }
  100% {
    opacity: 1;
    transform: rotateX(0);
  }
}

.navigation_fixed {
  position: fixed;
  width: 100%;
  height: 6rem;
  animation: position 0.5s ease forwards;
  top: 0;
  left: 0;
  right: 0;
  padding: 0 15%;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  transition: all 0.3s ease;
}

@keyframes move {
  from {
    transform: translate(-50%, 100%);
    opacity: 0;
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

.stages_active {
  visibility: visible;
  display: flex;
  animation: move 2.5s ease forwards;
  pointer-events: all;
  transition: all 0.3s ease;
}

.customer-logos {
  width: 100%;
  height: 35vh;
  display: flex;
  align-items: center;
}

.slick-track {
  height: 35vh;
}

.slick-slide {
  margin: 0px 20px;
  display: none;
  float: left;
  height: 90%;
  min-height: 1px;
  border-radius: 50%;
}

.slick-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.slick-slide .slick-loading img {
  display: none;
}

.slick-slide .dragging img {
  pointer-events: none;
}

.slick-slider {
  position: relative;
  display: flex;
  user-select: none;
  touch-action: pan-y;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list .dragging {
  cursor: no-drop;
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  display: flex;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
}

.slick-arrow.slick-hidden {
  display: none;
}

.cursor {
  pointer-events: none;
}

.cursor__ball {
  position: absolute;
  top: 0;
  left: 0;
  mix-blend-mode: difference;
  z-index: 9999;
}

.cursor__ball circle {
  fill: #e65c00;
  color: white;
}

.more {
  width: 100%;
  background-color: black;
  color: white;
  padding: 5% 0;
  overflow: hidden;
}

.more-wrapper {
  width: inherit;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.more__title {
  font-weight: 600;
  font-size: 3.5vw;
}

.more__title:first-letter {
  color: #e65c00;
}

.more-navigation {
  padding: 2rem;
  width: auto;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.more-navigation__item {
  list-style: none;
  width: auto;
  height: 100%;
  color: white;
  text-align: center;
  font-size: 1.35vw;
  padding: 1rem 2.5rem;
  position: relative;
  font-weight: bold;
}

.more-navigation__item:before {
  content: '';
  display: block;
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #e65c00;
  position: absolute;
  z-index: 2;
  animation: fadeHide 0.5s ease-in-out forwards;
}

.more-navigation__item:hover, .more-navigation__item:focus {
  color: white;
}

.more-navigation__item:hover span, .more-navigation__item:focus span {
  color: white;
  position: relative;
  z-index: 3;
}

.more-navigation__item:hover:before, .more-navigation__item:focus:before {
  animation: fade 1s ease-in-out forwards;
}

.more-list {
  width: inherit;
  height: 75vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.more-list__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  visibility: hidden;
  opacity: 0;
  transform-origin: bottom left;
  animation: dropped 2s ease forwards;
}

.more-list__item a {
  width: inherit;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}

.more-list__item a img {
  width: 95%;
  height: 100%;
}

.active {
  visibility: visible;
  transform-origin: top right;
  animation: blured 2.5s ease forwards;
  transition: all 0.5s ease;
  pointer-events: all;
}

.hero-stages__video-item {
  position: relative;
  top: 0;
  left: 0;
  transform: translateX(-50%);
  justify-content: space-between;
  align-items: flex-start;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  display: none;
}

.video-item--active {
  visibility: visible;
  opacity: 1;
  animation: position 1s ease forwards;
  transition: all 0.5s ease;
  pointer-events: all;
  left: 0;
  top: 0;
}

.modal {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(34, 40, 49, 0.9);
  z-index: 9998;
  transform: translateY(-100%);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.modal__form {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3.5rem 2rem;
  width: 50rem;
  border-radius: 15px;
  text-align: center;
  background-color: rgba(34, 40, 49, 0.85);
  box-shadow: 0 0 30px 1px rgba(230, 92, 0, 0.4);
}

.modal__title {
  padding-bottom: 2rem;
  color: white;
  font-size: 3.25rem;
  width: 80%;
}

.modal__input {
  padding: 1.5rem 2rem;
  margin: 1rem 0;
  font-size: 1.35vw;
  border: 1px solid #e65c00;
  transition: all 0.3s ease;
}

.modal__input:hover {
  box-shadow: 0 1px 15px 0 #e65c00;
}

.modal__btn {
  padding: 1rem 2.5rem;
  background-color: #fbd46d;
  background-image: linear-gradient(to right, #ffe259, #ffa751);
  border-radius: 5px;
  border: none;
  -webkit-backface-visibility: hidden;
  z-index: 1;
  width: 50%;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
  color: #222831;
  font-size: 2rem;
  margin-top: 2.5rem;
}

.modal__btn:after {
  content: '';
  position: absolute;
  display: block;
  border-radius: 5px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 5px 35px 1px rgba(255, 180, 0, 0.5);
  background: linear-gradient(to right, #e65c00, #f9d423);
  transition: all 1s ease-out;
  z-index: 2;
  opacity: 0;
}

.modal__btn:hover, .modal__btn:focus {
  color: #ffffff;
}

.btn--close {
  display: flex;
  text-align: center;
  width: 5rem;
  height: 5rem;
  background-color: transparent;
  transition: all 0.3s ease;
  font-size: 3.5rem;
  border: none;
  justify-content: center;
  align-items: center;
