  top: 5%;
  left: 95%;
  color: white;
  transform: translateX(-95%);
}

.btn--close:hover {
  background-color: #e65c00;
}

.modal--active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.scroll--hidden {
  overflow: hidden;
}

.btn--consultation {
  background-color: transparent;
  border-left: none;
  border-right: none;
  border-top: none;
}

